<template>
  <div>
    <!-- <TodoHeader></TodoHeader>
    <TodoHeader/>-->
    <todo-header></todo-header>
    <todo-input @add="addTodoItem"></todo-input>
    <todo-list :items="todoItems" @removeTodoItem="removeTodoItem"></todo-list>
    <todo-footer @clear:event="removeAllItems"></todo-footer>
  </div>
</template>

<script>
import TodoHeader from "@/components/TodoHeader.vue";
import TodoInput from "@/components/TodoInput.vue";
import TodoList from "@/components/TodoList.vue";
import TodoFooter from "@/components/TodoFooter.vue";

export default {
  data() {
    return {
      todoItems: []
    };
  },
  // 인스턴스 옵션 속성
  components: {
    // '컴포넌트 이름': 컴포넌트 내용,
    "todo-header": TodoHeader,
    "todo-input": TodoInput,
    "todo-list": TodoList,
    "todo-footer": TodoFooter
  },
  methods: {
    fetchItems() {
      for (var i = 0; i < localStorage.length; i++) {
        var item = localStorage.key(i);
        this.todoItems.push(item);
      }
    },
    addTodoItem(value) {
      this.todoItems.push(value); // 배열에 추가
      localStorage.setItem(value, value); // 저장소에 저장
    },
    removeTodoItem(text, index) {
      // TODO: 구현해볼것
      this.todoItems.splice(index, 1); // 목록에서 1개씩 삭제
      localStorage.removeItem(text); // 브라우저 저장소에서 1개씩 삭제
    },
    removeAllItems() {
      // TODO: 구현해볼것 - 모든 아이템 삭제
      this.todoItems = [];
      localStorage.clear();
    }
  },
  created() {
    this.fetchItems();
  }
};
</script>

